'use strict';
import { Rule } from "./rule";
export class Aijs {
    /**
     * 盤面を評価して-10000〜+10000で採点数する。
     * @param  {Object.<number, number>}  wkMap
     * @param  {boolean}  nearwin
     * @param  {Object.<number, Array.<number>>}  evalparam
     * @return {number}
     */
    static evalMap(wkMap, nearwin, evalparam) {
        let ev = 0;
        // 引き分け判定
        if (Rule.isDraw(wkMap)) {
            return 0;
        }
        // 終局判定
        let end = Rule.isEndX(wkMap, nearwin);
        if (end === 1) {
            return +9999999;
        }
        else if (end === -1) {
            return -9999999;
        }
        // 評価
        for (let i = 0; i <= 35; i++) {
            let panel_num = Rule.NUMBERS[i] | 0;
            let cell_p = 0;
            let p = wkMap[panel_num];
            let line;
            // コマの評価値を加算
            if (p > 0) {
                line = 5 - (panel_num % 10);
                cell_p += evalparam[p][line]; // ポジションボーナス
            }
            else if (p < 0) {
                line = (panel_num % 10);
                cell_p += evalparam[-1 * p][line] * -1;
            }
            // 評価値に加算。
            ev += cell_p;
        }
        return (ev | 0);
    }
    /**
     * よく考える
     */
    static deepThinkAllAB(map, turn_player, depth, a, b, nearwin, evalparam) {
        let best_score = turn_player * 9999999 * -1;
        let besthand;
        if (depth === 0) {
            best_score = Aijs.evalMap(map, nearwin, evalparam);
            return [besthand, best_score];
        }
        if (a === void 0 || b === void 0) {
            a = 9999999 * turn_player * -1;
            b = 9999999 * turn_player;
        }
        let nodeList = Rule.getNodeMap(map, turn_player);
        for (let i = 0; i < nodeList.length; i++) {
            let hand = nodeList[i][0];
            let map0 = nodeList[i][1];
            let sc = 0;
            // 必勝            
            let end = Rule.isEndX(map0, nearwin);
            if (end === turn_player) {
                return [hand, 999999 * turn_player];
            }
            // 必敗
            if (end === turn_player * -1) {
                if (besthand === void 0) {
                    best_score = 999999 * turn_player * -1;
                    besthand = hand;
                }
                continue;
            }
            if (Rule.isNoneNode(map0)) {
                sc = 0;
            }
            else {
                sc = Aijs.deepThinkAllAB(map0, turn_player * -1, depth - 1, b, a, nearwin, evalparam)[1];
            }
            if (besthand === void 0) {
                best_score = sc;
                besthand = hand;
            }
            if (turn_player === 1 && sc > best_score) {
                best_score = sc;
                besthand = hand;
            }
            else if (turn_player === -1 && sc < best_score) {
                best_score = sc;
                besthand = hand;
            }
            if (turn_player === 1 && a < best_score || turn_player === -1 && a > best_score) {
                a = best_score;
            }
            if (turn_player === 1 && b <= best_score || turn_player === -1 && b >= best_score) {
                break;
            }
        }
        return [besthand, best_score];
    }
    /**
     * 考える
     */
    static thinkAI(map, turn_player, depth, a, b, evalparam) {
        let nearwin = false;
        let hand = [undefined, 0];
        let wkMap = new Int8Array(map);
        if (!evalparam) {
            evalparam = Aijs.DEFAULT_EVALPARAM;
        }
        if (Rule.isEndX(wkMap, false) !== 0) {
            nearwin = true;
        }
        hand = Aijs.deepThinkAllAB(wkMap, turn_player, depth, a, b, nearwin, evalparam);
        if (hand[1] * turn_player === -999999) {
            hand = Aijs.deepThinkAllAB(wkMap, turn_player, 1, a, b, nearwin, evalparam);
        }
        return hand;
    }
}
/**
 * 初期評価関数
 * @const
 * @type {Object.<number, Array.<number>>}
 */
Aijs.DEFAULT_EVALPARAM = [
    [],
    [1800, 1850, 1900, 1950, 2100, 2800],
    [1800, 1860, 1920, 2100, 2400, 3800],
    [1450, 1520, 1590, 1900, 2350, 4450],
    [1450, 1530, 1610, 2050, 2650, 5450],
    [1350, 1440, 1530, 2100, 2850, 6350],
    [1350, 1450, 1550, 2250, 3150, 7350],
    [1250, 1360, 1470, 2300, 3350, 8250],
    [1250, 1370, 1490, 2450, 4350, 11250] //8
];
